//引入网络请求
import $H from '@/common/request.js';
import $C from '@/common/config.js';
import $U from '@/common/util.js';

import divider from '@/components/divider.vue';
export default {
	
	components:{
		divider
	},
	
	data() {
		return {
			background:{
				backgroundImage: 'linear-gradient(0deg, #FF7200 0%, #FFB183 100%)'
			},
			checked:true,
			show:false,
			checkedIndex:1,
			yuE:0,
			leiji:0,
			daiRu:0,
			kind:1,
			page:1,
			reload: true,
			loadMore: false,
			status: 'more',
			contentText: {
				contentdown: '查看更多',
				contentrefresh: '加载中',
				contentnomore: '没有更多'
			},
			listData:[]
		}
	},
	
	onLoad() {
		this.initData()
		this.getDataList()
	},
	
	
	onReachBottom() {
			this.loadMore = true;
			this.page++;  
			if (this.status == 'noMore') {
				return;
			}
			this.status = 'loading';
		    this.getDataList()
	},
	
	methods: {
		
		initData(){
			// 发送到服务端
			$H.get("/merchant/waterInit").then((res) => {
				//请求成功
				console.log(res)
				this.yuE=res.data.acount
				this.leiji=res.data.historyAcount
				this.daiRu=res.data.wati_acount
			}).catch((e) => {
				
				//请求失败
				console.log("失败"+e)
			})
		},
		
		
		
		
		getDataList(){
		    let data={
				kind:this.kind,
				size:10,
				current:this.page,
			}
			// 发送到服务端
			$H.get("/merchant/waterDetail",data).then((res) => {
				//请求成功
				console.log(res)
				let list = res.data.records
				
				let pages=res.data.pages;
				
				console.log(this.page)
				if(pages>1&&this.page<=pages){
					this.listData = this.reload ? list : this.listData.concat(list);
				}
				else if(pages==0){
					this.listData=[];
					this.nodata=true;
					this.status ='noMore'
				}
				else if(pages==1){
					this.listData = list
					this.status ='noMore'
				}
				else{
					this.status ='noMore'
				}
				
				this.reload = false;
				
			}).catch((e) => {
				
				//请求失败
				console.log("失败"+e)
			})
		},
		
		
		switchType(index){
			if(index==1){
				this.checked=true
				this.checkedIndex=1
				this.kind=1;
				this.page=1
				this.status = 'more';
				this.getDataList()
			}
			else{
				this.checked=false
				this.checkedIndex=2
				this.kind=0
				this.page=1
				this.status = 'more';
				this.getDataList()
			}
		},
		
		// 收款
		getMoney(){
			uni.showModal({
				title:"确定收款吗?",
				cancelText:"取消",
				confirmText:"收款",
				confirmColor:"#FF7200",
				success(res) {
					if(res.confirm){
						
					}
				}
			})
		},
		
		showDes(){
			this.show=!this.show
		}
	}
}
